import React, {useState, useEffect} from "react";
import Options from "./Options";
import Button from '@material-ui/core/Button';
import SearchIcon from '@material-ui/icons/Search';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

function Main()
{

  //Maintian the planets and vehicles options.
  var [planets, changeplanets] = useState([]);
  var [vehicles, changevehicles] = useState([]);

  //Maintian total time required for trip.
  var [totaltime, changetotaltime] = useState(0);

  //Maintian selection of the option for expedition.
  var[requestbody, changerequestbody] = useState({token:"", planet_names:[null,null,null,null], vehicle_names:[null,null,null,null]});

  //Maintian the result of expedition.
  var [result, changeresult] = useState();

  //Maintain display of result.
  const [open, setOpen] = React.useState(false);

  //TO load all the required data of of expedition.
  useEffect(()=>{

    fetch("/planets")
    .then( function(response) {
      if (response.status !== 200) {
        console.log('Looks like there was a problem. Status Code: ' +
          response.status);
        return;
      }

      // Examine the text in the response
      response.json().then(function(data) {
        changeplanets(data);
      });
    }
    )
    .catch(function(err) {
      console.log('Fetch Error :-S', err);
    });

    fetch("/vehicles")
    .then( function(response) {
      if (response.status !== 200) {
        console.log('Looks like there was a problem. Status Code: ' +
          response.status);
        return;
      }

      // Examine the text in the response
      //changeplanets(JSON.parse(planetrequest.responseText));
      response.json().then(function(data) {
        changevehicles(data);
      });
     }
    )
    .catch(function(err) {
     console.log('Fetch Error :-S', err);
    });

    fetch("/token", {
    method: 'post',
    headers: {
      "Accept": "application/json"
    }
    })
    .then(function (response) {
    if (response.status !== 200) {
      console.log('Looks like there was a problem. Status Code: ' +
        response.status);
      return;
    }

    // Examine the text in the response
    response.json().then(function(data) {
      changerequestbody((prev)=>{
      prev.token=data.token;
      return prev;
      });
    });
    })
    .catch(function (error) {
      console.log('Request failed', error);
    });
  },[]);

  //TO handle the find of falcone.
  function findfalcone(event)
  {
    event.preventDefault();

    fetch("/expedationresult", {
    method: 'post',
    headers: {
      "Accept": "application/json",
      "Content-Type": "application/json"
    },
    body: JSON.stringify(requestbody)
    } )
    .then(function (response) {
    if (response.status !== 200) {
        console.log('Looks like there was a problem. Status Code: ' +
        response.status);
        return;
    }

    // Examine the text in the response
    response.json().then(function(data) {
      changeresult(data);
      setOpen(true);
    });
    })
    .catch(function (error) {
      console.log('Request failed', error);
    });
  }

  //handle post result display event.
  const handleClose = () => {
    window.location.reload(false);
  };

    return (
      <form className="form" onSubmit={findfalcone}>
    <div className="main">
    <Options index={0} planets={planets} changeplanets={changeplanets} vehicles={vehicles} changevehicles={changevehicles} requestbody={requestbody} changerequestbody={changerequestbody} totaltime={totaltime} changetotaltime={changetotaltime}/>
    <Options index={1} planets={planets} changeplanets={changeplanets} vehicles={vehicles} changevehicles={changevehicles} requestbody={requestbody} changerequestbody={changerequestbody} totaltime={totaltime} changetotaltime={changetotaltime}/>
    <Options index={2} planets={planets} changeplanets={changeplanets} vehicles={vehicles} changevehicles={changevehicles} requestbody={requestbody} changerequestbody={changerequestbody} totaltime={totaltime} changetotaltime={changetotaltime}/>
    <Options index={3} planets={planets} changeplanets={changeplanets} vehicles={vehicles} changevehicles={changevehicles} requestbody={requestbody} changerequestbody={changerequestbody} totaltime={totaltime} changetotaltime={changetotaltime}/>
    <h2>Time taken: {totaltime}</h2>
    </div>
    <div className="btn">
    {(requestbody.token==="" || requestbody.planet_names.includes(null) || requestbody.vehicle_names.includes(null))? <Button
        variant="contained"
        color="default"
        type="submit"
        disabled
        endIcon={<SearchIcon />}
      >
        Find Falcone
      </Button>
      :<Button
        variant="contained"
        color="default"
        type="submit"
        endIcon={<SearchIcon />}
      >
        Find Falcone
      </Button>}
      <div>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">expedition result</DialogTitle>
        <DialogContent>
          <DialogContentText>
          {(result==="success")?"Found Falcon in planet: " + result.planet_name:"Sorry, couldn't find flacon"}.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </div>
    </div></form>
    );
}

export default Main;