import React from "react";

function Header()
{
    function reset()
    {
        window.location.reload(false);
    }
    return (
        <div>
        <nav className="header">
            <label onClick={reset}>Reset</label>
            <a href="https://csb-ffsy5.netlify.app/">Resume</a>
        </nav>
        </div>
    );
}

export default Header;